import noData from '@/components/noData'
import back from '@/components/back'

export default {
    components:{
        noData,
        back
    },
    data(){
        return{
			dconfirmShow:false,
			user: this.$store.state.userInfo,
			FaceId: this.$store.state.FaceId,
			HospitalName:null,
			IsOpen:false,
			IsFree:false,
			AppointmentPrice:null,
			AppointmentDiscountPrice:null,
        }
    },
    created(){
		if(this.$store.state.saveFaceSet){
			this.showFaceSet()
		}else{
			this.pull()
		}
    },
    mounted(){
    },
    beforeDestroy(){
        window.onscroll = null
    },
    methods: {
		onCancel(){
			this.$router.back()
		},
		pull(){
			this.$get('AppointmentAPI/FaceConsultation/GetFaceConsultationAddress',{
				FaceConsultationAddressId:this.FaceId,
			}).then(o => {
				this.HospitalName = o.HospitalName
				if(o.IsOpen){
					this.IsOpen = true
				}else{
					this.IsOpen = false
				}
				if(o.IsFree){
					this.IsFree = true
				}else{
					this.IsFree = false
				}
				this.AppointmentPrice = o.AppointmentPrice == '0.00' ? 0 : o.AppointmentPrice
				this.AppointmentDiscountPrice = o.AppointmentDiscountPrice == '0.00' ? 0 : o.AppointmentDiscountPrice
				this.goTo()
			})
		},
		goTo(){
			let arr = {
				HospitalName:this.HospitalName,
				IsOpen:this.IsOpen,
				IsFree:this.IsFree,
				AppointmentPrice:this.AppointmentPrice,
				AppointmentDiscountPrice:this.AppointmentDiscountPrice
			}
			this.$store.dispatch('pushsaveFaceSet',arr)
		},
		goScheduling(){
			this.goTo()
			this.$router.push('/faceConsultation/scheduling')
		},
		showFaceSet(){
			const list = this.$store.state.saveFaceSet
			this.HospitalName = list.HospitalName
			this.IsOpen = list.IsOpen
			this.IsFree = list.IsFree
			this.AppointmentPrice = list.AppointmentPrice
			this.AppointmentDiscountPrice = list.AppointmentDiscountPrice
		},
		SetFaceConsultation(){
			let IsOpenNum = 0
			if(this.IsOpen){
				IsOpenNum = 1
			}
			let IsFreeNum = 0
			if(this.IsFree){
				IsFreeNum = 1
			}
			this.$post('AppointmentAPI/FaceConsultation/SetFaceConsultationAddress',{
				FaceConsultationAddressId:this.FaceId,
				IsOpen:IsOpenNum,
				AppointmentPrice:this.AppointmentPrice,
				AppointmentDiscountPrice:this.AppointmentDiscountPrice,
				IsFree:IsFreeNum
			}).then(o => {
				this.$store.dispatch('pushsaveFaceSet',null)
				this.$router.push('/faceConsultation/SetUp')
			})
		},
    }
}



